import React from 'react';

import {
  Button,
  Container,
  Col,
  Form,
  Row,
} from 'react-bootstrap';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import products from './products';

const Product = ({ name, code }) => (
  <Col xs = {6} lg = {4}>
    <img
      width = "100%"
      src   = "/assets/dates.jpg"
      alt   = ""
      style = {{
        border: '1px solid #cfcfcf',
      }}
    />
    <div
      className = "text-primary"
      style     = {{
        fontSize: '1rem',
        fontWeight: '500',
        marginTop: '1rem',
        marginBottom: '.2rem',
      }}
    >
      {name}
    </div>
    <div
      className = "text-primary"
      style     = {{
        fontSize: '1rem',
        fontWeight: '200',
        marginBottom: '1rem',
      }}
    >
      Code: {code}
    </div>
  </Col>
);

export default class Products extends React.Component {
  constructor() {
    super();

    this.state = {
      filters: [],
    };

    this.categories = [];

    products.forEach(product => {
      if (! this.categories.includes(product.category))
        this.categories.push(product.category);
    });
  }

  render() {
    const { filters } = this.state;

    return (
      <Container
        id    = "products"
        style = {{
          paddingTop: '4rem',
          paddingBottom: '4rem',
        }}
      >
        <Row>
          <Col>
            <h1
              className = "text-primary"
              style     = {{
                marginBottom: '1.5rem',
              }}
            >
              Our Products
            </h1>
          </Col>
        </Row>
        <Row>
          <Col
            className = "d-none d-md-block"
            md        = {3}
          >
            <Button
              className = "text-light background-primary"
              variant   = "link"
              style     = {{
                fontWeight: 500,
              }}

              onClick = {() => this.setState({ filters: [] })}
            >
              <FontAwesomeIcon
                icon  = "times-circle"
                style = {{
                  marginRight: '.5rem',
                }}
              />
              Clear
            </Button>
            <h6 style = {{ marginTop: '.5rem' }}>Categories</h6>
            {this.categories.map((category, i) =>
              <Form.Check
                key  = {i}
                type = "checkbox"
              >
                <Form.Check.Input
                  id       = {`category-${i}`}
                  type     = "checkbox"
                  checked  = {filters.includes(category)}
                  onChange = {e => {
                    const filter = e.target.nextSibling.innerText,
                          index  = filters.indexOf(filter);

                    if (-1 === index) filters.push(filter);
                    else filters.splice(index, 1);

                    this.setState({ filters });
                  }}
                />
                <Form.Check.Label
                  htmlFor = {`category-${i}`}
                  style   = {{
                    cursor: 'pointer',
                    userSelect: 'none',
                  }}
                >
                  <h6>{category}</h6>
                </Form.Check.Label>
              </Form.Check>
            )}
          </Col>
          <Col
            xs = {12}
            md = {9}
          >
            <Row>
            {products.map((product, i) => (! filters.length || filters.includes(product.category)) && <Product key = {i} { ...product } />)}
            </Row>
          </Col>
        </Row>
      </Container>
    );
  }
}
